		/*------倒计时器-----*/
		CountDown() {
			var self = this;
			// var second = self.timestamp%60;
			var sec = setInterval(() => {
				if (self.sec > 0) {
					self.sec--;
					self.minText = self.min;
					if (self.sec < 10) {
						self.secText = "0" + self.sec;
					} else {
						self.secText = self.sec;
					}
				} else {
					if (self.min > 0) {
						self.min--;
						self.sec = 60;
						if (self.min < 10) {
							self.minText = "0" + self.min;
						} else {
							self.minText = self.min;
						}
					} else {
						self.minText = "00";
						self.secText = "00"
						clearInterval(sec);
					}
				}


			}, 1000)

			console.log(self.sec, self.min, "CountDown");
		},
		/*------倒计时器-----*/

		complaint() {
			var self = this;
			uni.showModal({
				title: "投诉",
				content: "如遇上传虚假凭证者，可申请投诉",
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						self.$service.auth(self, self.$service.api.record.orderComplaint, 'POST', {
							id: parseInt(self.id),
							reason: '对方上传假凭证'
						}, function(self, res) {
							console.log(res);
							uni.showToast({
								title:res.info,
								icon:'none'
							});
							setTimeout(()=>{
								uni.navigateTo({
									url:'/pages/center/my_deal?type='+ 1
								});
							},1500);
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});

		},
