		commit() {
			uni.showModal({
				title: "确认付款",
				content: "是否确认付款，点击确认完成置换",
				success: (res) => {
					if (res.confirm) {
						uni.uploadFile({
							url: this.$service.api.record.getTransactionPay, //
							filePath: this.SrcImage,
							header: {
								Authorization: 'Bearer ' + uni.getStorageSync('token')
							},
							name: 'file',
							formData: {
								id: parseInt(this.id),
							},
							success: (res) => {
								const ress = JSON.parse(res.data);
								uni.showToast({
									title: ress.info,
									icon: 'none'
								})
								setTimeout(() => {
									uni.navigateTo({
										url:'/pages/center/my_deal?type='+ 1
									});
								}, 1500);
								console.log(ress.data);
							}
						});
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		chooseIm() {
